








































































































import { Vue, Component } from "vue-property-decorator";
import axios from "axios";
import api from '@/api'

@Component
export default class User extends Vue {
  users: any[] = [];
  //总条数
  count: number = 0;
  //每页显示条数
  limit: number = 0;
  //当前页码
  page: number = 1;

  //对话框的状态
  dialogFormVisible: boolean = false;

  //用户详情
  detail = {};

  created() {
    this.getUsers(this.page);
  }

  //获取用户数据
  async getUsers(n: number) {
    let res = await axios({
      method: "get",
      url:api.user.list+ "?page=" + n
    });

    if (res.data.code === 0) {
      //记录总条数
      this.count = res.data.users.count;
      this.limit = res.data.limit;
      //用户列表数据
      //后端的数据与前端的ui 有非直接的对应关系,我们可以把这个数据进行一些处理
      this.users = res.data.users.rows.map((row: any) => {
        return Object.assign(row, {
          status: !row.disabled
        });
      });
      console.log(this.users);
    }
  }

  //更改用户状态

  async changeStatus(user: any) {
    let rs = await axios({
      method: "post",
      url: api.user.status,
      data: {
        id: user.id
      }
    });
  }

  //切换页码
  async currentChange(v: number) {
    console.log(v);
    //根据当前v的值重新发送新的请求
    this.getUsers(v);
  }

  //展示详情:  查看用户详情
  showDetail(user: any) {
    console.log(user);

    this.dialogFormVisible = true;

    this.detail = {
      id: user.id,
      avatar: user.avatar,
      realname: user["user-profile"].realname,
      mobile: user["user-profile"].mobile,
      gender: user["user-profile"].gender,
      email: user["user-profile"].email,
      birthday: user["user-profile"].birthday
    };
  }

  //修改用户详情
  async editDetail() {
    // this.dialogFormVisible = true;

    //更改用户状态的发送请求
    //不推荐 把头像也传递过去(payload),可以直接在前端更换上传头像的功能,可以单独处理头像数据,发到后端后端可以不用处理

    // v-loading 当使用指令方式时，全屏遮罩需要添加fullscreen修饰符（遮罩会插入至 body 上），此时若需要锁定屏幕的滚动，
    //可以使用lock修饰符；当使用服务方式时，遮罩默认即为全屏，无需额外设置

    //发送请求之前先发送一个loading
    const loading = this.$loading({
      lock: true,
      text: "Loading",
      spinner: "el-icon-loading",
      background: "rgba(0, 0, 0, 0.7)"
    });

    let rs = await axios({
      method: "post",
      url: api.user.profile,
      data: this.detail
    });

    //如果当前发送请求没错的话,就是 发送成功了,那么关闭loading 状态
    //对话框也要关闭,  整个一个流程
    //增加一个后端接口
    if (!rs.data.code) {
      
      loading.close();
      this.dialogFormVisible = false;


      //展示用户信息中  使用的不应该是原来的数据
      let user = this.users.find(  u => u.id === rs.data.data.userId )

      // console.log(user);
      console.log( rs.data.data.birthday);
      console.log(this.detail);
      
      
      
      //把返回的数据更新一下
      user['user-profile'] = {
        realname: rs.data.data.realname,
        mobile: rs.data.data.mobile,
        email: rs.data.data.email,
        gender: rs.data.data.gender,
        birthday: rs.data.data.birthday,
      }


      // Object.assign(this.detail,)

      // this.detail = {
      //   id: rs.data.data.id,
      //   avatar: rs.data.data.avatar,
      //   realname: rs.data.data["user-profile"].realname,
      //   mobile: rs.data.data["user-profile"].mobile,
      //   gender: rs.data.data["user-profile"].gender,
      //   email: rs.data.data["user-profile"].email,
      //   birthday: rs.data.data["user-profile"].birthday
      // };
    }
  }
}
