




























import { Vue, Component } from "vue-property-decorator";
import axios from 'axios';
import api from '@/api'

@Component
export default class Category extends Vue {
  categories: any[] = [];

  //发送请求到后端,把获取到的真实的分类数据赋值给组件的 categories
  async created() {
    this.getCategories();
  }

  async getCategories() {
    let res = await axios({
      method: "get",
      url: api.category.getCategory,
    });
    // this.categories = res.data.data
    // console.log(res.data.data[0]);
    if (res.data.code == 0) {
      this.categories = res.data.data ;
    }
  }

  //添加分类

  async add ( pid: number ) {

    //promise 一定要用try catch 捕获异常

    try {
      //通过当前  $prompt 来显示一个对话框

      let { value } = await this.$prompt('请输入分类名称','添加分类',{
        // inputPattern: /\w+/,
        inputErrorMessage: '分类信息不能为空'
      })

      // alert(value)

      let { data } = await axios ({
        method: "post",
        url: api.category.add,
        data: {
          name: value,
          pid
        }
      });

      if (data.code) {
        //存在code ,错误
        this.$notify.error( data.message );

      }else{
        //添加成功的
        this.getCategories();
        this.$notify.success( '添加成功~~' );
      }
    } catch (error) {
      console.log(error);
      this.$notify.success( "取消了~~" );
    }
    


  }

  //修改分类
  async edit( category: any) {
    try {
      let {value} = await this.$prompt('请输入修改分类名称','修改分类',{
        inputPattern: /[\S+]+/,
        inputErrorMessage: '分类名称不能为空',
        inputValue: category.name
      })

      if (value === category.name) {
        return ;
      }

      let {data} = await axios({
        method: 'post',
        url: api.category.edit,
        data: {
          id: category.id,
          name: value,
        }
      })

      if (data.code) {
        //存在code ,错误
        this.$notify.error( data.message );

      }else{
        //添加成功的
        this.getCategories();
        this.$notify.success( '修改成功~~' );
      }

    } catch (error) {
      
    }
  }
  //删除分类
  async remove( id: number) {
    try {
      await this.$confirm('你确定要删除吗?','删除分类');
      console.log('即将删除~~');

      let {data} = await axios({
        method: 'post',
        url: api.category.remove,
        data: {
          id
        }
      })

      if (data.code) {
        //存在code ,错误
        this.$notify.error( data.message );

      }else{
        //添加成功的
        this.getCategories();
        this.$notify.success( '删除成功~~' );
      }
      
    } catch (error) {
      this.$notify.warning('取消了~~')
    }
  }



}
