


























import { Vue, Component } from 'vue-property-decorator';

@Component
export default class AppAside extends Vue {
  menus: any[] = [];

  created () {
    this.menus = this.$store.state.menus;
  }

  /* get 计算属性 */
  get routerName () {
    return this.$store.state.router.name;
  }
