let hostUrl ="";
if (process.env.NODE_ENV == 'development') {
  hostUrl = '/dev/'
}else{
  hostUrl = '/'
}

export default {
  user: {
    list: hostUrl + 'api/admin/user',
    status: hostUrl + 'api/admin/user/status',
    profile: hostUrl + 'api/admin/user/profile'
  },
  category: {
    getCategory: 'api/admin/category',
    add: 'api/admin/category/add',
    edit: 'api/admin/category/edit/',
    remove: 'api/admin/category/remove'
  }
}